* {
  box-sizing: border-box;
}
body {
  margin: 0;
}
.App {
  font-family: sans-serif;
  text-align: center;
}
.SplashScreen {
  display: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: #860038;
}

.Logo {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 400px;
  height: 200px;
  opacity: 0;
  transform: translate(-50%, -50%) scale(1);
  background-color: white;
  border-radius: 100px;
  animation: 5s expand 1s infinite;
}
.Logo .left {
  position: absolute;
  top: 0;
  left: 0;
  width: 200px;
  height: 200px;
}
.Logo .left img {
  width: inherit;
  height: inherit;
}
.Logo .right {
  position: absolute;
  top: 0;
  left: 200px;
  width: 200px;
  height: 200px;
  padding-top: 40px;
}
.Logo .right span {
  font-size: 3em;
  color: #860038;
  font-variant: small-caps;
  line-height: 40px;
}

@keyframes expand {
  0% {
    width: 200px;
    transform: translate(-50%, -50%) scale(1);
    opacity: 0;
  }
  10% {
    width: 200px;
    transform: translate(-50%, -50%) scale(1);
    opacity: 1;
  }
  31% {
    width: 200px;
    transform: translate(-50%, -50%) scale(1);
    opacity: 1;
  }
  35% {
    width: 200px;
    transform: translate(-50%, -50%) scale(0.9);
    opacity: 1;
  }
  50% {
    width: 400px;
    transform: translate(-50%, -50%) scale(1);
    opacity: 1;
  }
  90% {
    width: 400px;
    transform: translate(-50%, -50%) scale(1);
    opacity: 1;
  }
  100% {
    width: 400px;
    transform: translate(-50%, -50%) scale(1);
    opacity: 0;
  }
}
